	}

	#placeos .sm\:flex-none {
		flex: none;
	}

	#placeos .sm\:flex-1 {
		flex: 1 1 0%;
	}

	#placeos .sm\:flex-row {
		flex-direction: row;
	}

	#placeos .sm\:space-x-2 > :not([hidden]) ~ :not([hidden]) {
		--tw-space-x-reverse: 0;
